<template>
  <div class="container" style="margin: 0 5%">
    <div
      @click="backTop"
      v-show="flag_scroll"
      style="
        width: 30px;
        height: 30px;
        position: fixed;
        right: 13px;
        bottom: 70px;
        background: rgb(214 225 199 / 40%);
        border-radius: 50%;
        line-height: 30px;
      "
    >
      <span style="font-size: 20px; color: #308ae3"
        ><i class="iconfont icon-dingbu"></i
      ></span>
    </div>
    <!-- 筛选栏 -->

    <div style="margin-bottom: 10px">
      <!-- 第一行筛选 -->
      <div class="firstSelect" v-if="SelectTopList.length > 0">
        <p :class="{ activeCss: firstActive == 0 }" @click="sortV(0, 1)">
          {{ SelectTopList[0][0].name }}
        </p>
        <p>|</p>
        <p :class="{ activeCss: firstActive == 1 }" @click="sortV(1, 3)">
          {{ SelectTopList[0][1].name }}
        </p>
        <div v-if="!popupShow" class="selectButton" @click="showPopup">
          ▼筛选
        </div>
        <div v-else class="selectButton" @click="showPopup">▲筛选</div>
      </div>

      <div class="selectPopup" v-show="popupShow">
        <!-- 第2行筛选 -->
        <div class="twoSelect">
          <div v-for="(item, index) in SelectBottomList[0]" :key="index">
            <p
              :class="{ activeCss: twoActive == index }"
              @click="twoV(index, item.value)"
            >
              {{ item.name }}
            </p>
          </div>
        </div>
        <!-- 第3行筛选 -->
        <div class="threeSelect" v-if="SelectBottomList[1]">
          <div v-for="(item, index) in SelectBottomList[1]" :key="index">
            <p
              :class="{ activeCss: threeActive == index }"
              @click="threeV(index, item.value)"
            >
              {{ item.name }}
            </p>
          </div>
        </div>
        <!-- 第4行筛选 -->
        <div class="fourSelect" v-if="SelectBottomList[2]">
          <div v-for="(item, index) in SelectBottomList[2]" :key="index">
            <p
              :class="{ activeCss: fourActive == index }"
              @click="fourV(index, item.value)"
            >
              {{ item.name }}
            </p>
          </div>
        </div>
      </div>
    </div>
    <!-- 书籍列表 -->

    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <!-- 空状态:加载中 -->
      <!-- <van-empty v-if="nullStatus" description="加载中" /> -->
      <!-- 空状态:匹配不到 -->
      <van-empty v-if="nullStatus2" description="没有找到你想要的内容" />
      <!-- 渲染的书籍列表 -->
      <van-row
        gutter="20"
        style="
          padding: 0 5%;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
        "
      >
        <van-col
          span="8"
          v-for="(book, index) in bookList"
          :key="index"
          style="margin-bottom: 10px; width: 30%"
          @click="enterBookDetail(book.id)"
        >
          <img
            width="100%"
            :src="book.cover + '?imageView&thumbnail=240y336'"
            @error="errorImg"
          />

          <p
            style="
              text-align: left;
              font-size: 14px;
              margin-top: 10px;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            "
          >
            {{ book.title }}
          </p>
          <p
            style="
              text-align: left;
              font-size: 14px;
              color: gray;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            "
          >
            {{ book.author }}
          </p>
        </van-col>
        <van-col span="8" style="margin-bottom: 10px; width: 30%"> </van-col>
      </van-row>
    </van-list>
    <div class="bottom_trim" style="margin-top: 50px"></div>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {
      SelectTopList: [],
      SelectBottomList: [],
      // 畅销最新默认为0
      firstActive: 0,
      // 第2行筛选栏
      twoActive: 0,
      // 第3行筛选栏
      threeActive: 0,
      // 第4行筛选栏
      fourActive: 0,
      popupShow: false,
      // 列表
      loading: false,
      finished: false,
      // 页码
      pageValue: 1,
      // 书籍列表
      bookList: [],
      // 空状态
      nullStatus: true,
      nullStatus2: false,
      // 筛选栏的对应值value
      // 0全部 1收费 2免费 3包月
      payValue: 0,
      // 1畅销 3最新
      sortValue: 1,
      // 顶部按钮的显示隐藏
      flag_scroll: false,
    };
  },
  computed: {
    ...mapState(["publishUrl"]),
  },
  created() {
    this.bookList = [];
    this.axios
      .get(
        `https://apis.netstart.cn/yunyuedu/source/v2/cat.json?catId=${this.publishUrl}`
      )
      .then((res) => {
        this.$nextTick(() => {
          console.log(res.data.categories);
          res.data.categories.forEach((element) => {
            console.log(element);
            if (element.isTop) {
              this.SelectTopList.push(element.catArray);
            } else {
              this.SelectBottomList.push(element.catArray);
            }
          });
        });
      });
  },
  methods: {
    // 切换筛选条件
    changeSelect() {
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/source/v2/searchBook.json?catId=${this.publishUrl}&pay=${this.payValue}&sort=${this.sortValue}&page=${this.pageValue}`
        )
        .then((res) => [
          this.$nextTick(() => {
            if (res.data.list) {
              this.bookList.push(...res.data.list);
              this.pageValue++;
              // 加载状态结束
              this.loading = false;
            } else {
              this.loading = false;
              this.finished = true;
            }
          }),
        ]);
    },
    // 加载书籍数据
    onLoad() {
      this.axios
        .get(
          `https://apis.netstart.cn/yunyuedu/source/v2/searchBook.json?catId=${this.publishUrl}&pay=${this.payValue}&sort=${this.sortValue}&page=${this.pageValue}`
        )
        .then((res) => {
          this.$nextTick(() => {
            if (res.data.list) {
              this.bookList.push(...res.data.list);
              this.pageValue++;
              // 加载状态结束
              this.loading = false;
            }
            if (res.data.list == null || res.data.list == undefined) {
              this.loading = false;
              this.finished = true;
            }
          });
        });
    },
    // 回到屏幕顶部
    backTop() {
      window.scrollTo(0, 0);
    },
    // 图片挂了的备用封面
    errorImg(event) {
      let img = event.srcElement;
      img.src = require("@/assets/img/book_cover.png");
      img.onerror = null; //防止一直跳动
    },
    // 进入书籍详情
    enterBookDetail(bookId) {
      this.$router.push({
        name: "bookdetail",
        query: {
          id: bookId,
        },
      });
    },

    showPopup() {
      this.popupShow = !this.popupShow;
    },
    // 第一行点击方法
    sortV(index, value) {
      if (this.firstActive == index) {
        return;
      } else {
        this.firstActive = index;
        this.sortValue = value;
        this.pageValue = 1; // 发送筛选后的数据请求
        this.finished = false;
        this.bookList = [];
        this.changeSelect();
      }
    },
    // 第2行点击方法
    twoV(index, value) {
      if (this.twoActive == index) {
        return;
      } else {
        this.twoActive = index;
        this.$store.dispatch("getPublishUrl", value);
        this.pageValue = 1; // 发送筛选后的数据请求
        this.finished = false;
        this.bookList = [];
        this.changeSelect();
      }
    },
    // 第3行点击方法
    threeV(index, value) {
      if (this.threeActive !== index) {
        this.threeActive = index;
        this.payValue = value;
        this.pageValue = 1; // 发送筛选后的数据请求
        this.finished = false;
        this.bookList = [];
        this.changeSelect();
      }
    },

    // 滚动一定距离出现返回顶部按钮
    handleScroll() {
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      if (scrollTop > 700) {
        this.flag_scroll = true;
      } else {
        this.flag_scroll = false;
      }
    },
  },
  // 监听滚动一定距离出现返回顶部按钮
  mounted() {
    window.addEventListener("scroll", this.handleScroll, true);
  },
  destroyed() {
    window.removeEventListener("scroll", this.handleScroll); //销毁滚动事件
  },
};
</script>

<style lang="less">
.activeCss {
  color: red;
}
.firstSelect {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  padding-bottom: 4px;
  p {
    margin-right: 9px;
  }
  .selectButton {
    position: absolute;
    right: 0;
    border: 1px solid black;
    border-radius: 12px;
    padding: 2px 4px;
    color: gray;
  }
}
.twoSelect,
.threeSelect,
.fourSelect {
  display: flex;
  flex-wrap: wrap;
  p {
    margin-right: 20px;
    padding: 4px 0;
  }
}
</style>